const header = document.getElementById("header");
window.addEventListener("scroll", function () {
    if (scrollY > 50) {
        header.style.backgroundColor = "rgba(74, 108, 247, .2)"
        header.style.backdropFilter = "blur(15px)"
    } else {
        header.style.backgroundColor = "transparent"
        header.style.backdropFilter = "blur(0px)"
    }
})

var earlyAccessFormFilled = localStorage['earlyAccessFormFilled'] || false
EARLY_ACCESS_DATA = {}

const crmButtons = document.querySelectorAll('.access-form-crm-button')
const crmPage = document.getElementById("get-early-access-page-1")
const channelButtons = document.querySelectorAll('.access-form-channel-button')
const channelPage = document.getElementById("get-early-access-page-2")
const crmEmailId = document.getElementById("get-early-access-email")
const crmSubmitButton = document.getElementById("get-early-access-submit")
const emailPage = document.getElementById("get-early-access-page-3")
const finalPage = document.getElementById("get-early-access-page-4")

if (earlyAccessFormFilled) {
    finalPage.removeAttribute("hidden")
} else {
    crmPage.removeAttribute("hidden")
}

crmButtons.forEach(crmButton => {
    crmButton.addEventListener('click', () => {
        EARLY_ACCESS_DATA['crm'] = crmButton.value
        crmPage.setAttribute("hidden", true)
        channelPage.removeAttribute("hidden")
    });
});
channelButtons.forEach(channelButton => {
    channelButton.addEventListener('click', () => {
        EARLY_ACCESS_DATA['channel'] = channelButton.value
        channelPage.setAttribute("hidden", true)
        emailPage.removeAttribute("hidden")
    });
});
crmSubmitButton.addEventListener('click', () => {
    console.log(crmEmailId.validity.valid)
    if (crmEmailId.validity.valid) {
        EARLY_ACCESS_DATA['email'] = crmEmailId.value
        console.log(EARLY_ACCESS_DATA)
        emailPage.setAttribute("hidden", true)
        finalPage.removeAttribute("hidden")
        localStorage['earlyAccessFormFilled'] = true
    } else {
        crmEmailId.classList.add("border-red-600")
    }
})
